form {
  display: flex;
  width: 100%;
  flex-direction: column;
  height: 70%;
  font-size: 1.3vmax;
}

form > h2 {
  margin: 1% 0 3% 0;
}

form label {
  margin: 5% 0 3% 0;
}

form input[type="text"] {
  /* margin: 5% 0; */
  font-size: 1.1vmax;
  padding: 2%;
}

form textarea {
  font-size: 1.1vmax;
  padding: 2%;
}

form input[type="submit"] {
  margin: 5% 0;
  font-size: 1.1vmax;
}
