.registerContainer {
  width: 40%;
  height: 60%;
  margin: auto;
  padding: 0 5%;
}

.registerForm {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-around;
}

.registerForm > h2 {
  font-size: 2.5vmax;
  margin: 0% 0 7% 0;
  text-align: center;
}

.registerForm input {
  font-size: 1vmax;
  padding: 2%;
}

.registerForm a {
  text-align: end;
  font-size: 1.1vmax;
}
